.infobox {
    &.style_7 {
        padding: 30px;
        background-color: #fff;
        &.align_center {
            text-align: center;
        }
        .infobox_title {
            font-size: 24px;
            line-height: 1.08;
            padding-top: 0;
        }
        .infobox_content {
            font-size: 16px;
            color: #304157;
            line-height: 1.63;
            margin-bottom: 20px;
        }
        .infobox_link {
            font-size: 14px;
            line-height: 1.57;
            color: #304157;
            i {
                font-size: 10px;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                text-align: center;
                margin-right: 4px;
                display: inline-block;
                line-height: 20px;
                transition: .3s;
            }
            &:hover {
                text-decoration: none;
                i {
                    background: #304157 !important;
                    color: #fff;
                }
            }
        }
    }
}