.staff_list {
    &.minimal {
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                display: flex;
                flex-wrap: wrap;
                .staff_image {
                    width: 120px;
                    flex: 0 0 120px;
                }
                .staff_info {
                    width: calc(100% - 120px);
                    flex: 0 0 calc(100% - 120px);
                    padding-left: 23px;
                    p, .read_more {
                        display: none !important;
                    }
                    h4 {
                        font-size: 24px;
                        line-height: 1.08;
                        margin-bottom: 5px;
                    }
                    .staff_department {
                        font-size: 16px;
                        line-height: 1.25;
                        color: #304157;
                    }
                }
            }
        }
    }
}